@font-face {
    src: url('./fonts/lemon-milk.woff') format('woff');
    src: url('./fonts/lemon-milk.woff2') format('woff2');
    font-family: 'Lemon Milk';
}

// stylelint-disable value-keyword-case
$font__main: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
// stylelint-enable value-keyword-case
$font__code: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
$font__pre: "Courier 10 Pitch", courier, monospace;
$font__line-height-body: 1.5;
$font__line-height-pre: 1.6;

// Fonts
$headerFont: 'Raleway', sans-serif;

$contentFont: 'Raleway', sans-serif;
